@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@700&family=Signika:wght@600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.card {
  outline: none;
  border: none;
  width: 320px;
  height: 450px;
  margin-top: 80px;
  overflow: hidden;
  box-shadow: 0px 5px 20px rgba(0, 0, 0, 0.5);
  position: relative;
  left: 50px;
  font-family: "Signika", sans-serif;
  border-radius: 15px;
}
.data .boxer {
  width: 320px;
  height: 450px;
  background: #fff;
  overflow: hidden;
}

.data .boxer:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #4e73df;
  -webkit-clip-path: circle(150px at 80% 20%);
          clip-path: circle(150px at 80% 20%);
  transition: 0.5s ease-in-out;
}

.data .boxer:hover:before {
  -webkit-clip-path: circle(300px at 80% -20%);
          clip-path: circle(300px at 80% -20%);
}

.data .boxer:after {
  content: "APSSDC";
  position: absolute;
  top: 55%;
  left: -2%;
  font-size: 5.5em;
  font-weight: 800;
  font-style: italic;
  color: rgba(0, 0, 0, 0.2);
  z-index: 1;
}

.data .boxer .imgBx {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  z-index: 1000;
  width: 100%;
  height: 100%;
  transition: 0.5s;
}

.data .boxer:hover .imgBx {
  top: 0%;
  transform: translateY(-25%);
  /* bug  */
}

.data .boxer .imgBx img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%); /*rotate(20deg)*/
  border-radius: 50%;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.4);
  width: 220px;
}

.data .boxer .contentBx {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 100px;
  text-align: center;
  transition: 1s;
  z-index: 99999;
}

.data .boxer:hover .contentBx {
  height: 195px;
}
.data .boxer:hover .contentBx h1,
.data .boxer:hover .contentBx h3,
.data .boxer:hover .contentBx h2,
.data .boxer:hover .contentBx button {
  opacity: 1;
  visibility: visible;
  transition-delay: 0.3s;
}
.data .boxer .contentBx h1 {
  font-size: 30px;
  transition: 0.5s;
  opacity: 0;
  visibility: hidden;
}

.data .boxer .contentBx h3 {
  font-size: 20px;
  transition: 0.5s;
  opacity: 0;
  visibility: hidden;
  cursor: pointer;
}
.data .boxer .contentBx .email {
  font-size: 15px;
}

.data .boxer .contentBx h2 {
  font-size: 18px;
  transition: 0.5s;
  opacity: 0;
  visibility: hidden;
}

.data .boxer .contentBx button {
  transition: 0.5s;
  opacity: 0;
  visibility: hidden;
  letter-spacing: 1px;
  width: 90px;
}
.link:after {
  content: "";
  position: absolute;
  left: 34%;
  display: block;
  width: 0;
  height: 2px;
  transition: width 0.3s;
  background: #4e73df;
}
.link:hover::after {
  width: 33%;
  transition: width 0.3s;
}
.link:hover {
  color: #000;
  text-decoration: none;
}
.link1:after {
  content: "";
  position: absolute;
  left: 17%;
  display: block;
  width: 0;
  height: 2px;
  transition: width 0.3s;
  background: #4e73df;
}
.link1:hover::after {
  width: 66%;
  transition: width 0.3s;
}
.link1:hover {
  color: #000;
  text-decoration: none;
}

.new-right-side {
  width: 700px;
}

.card-body img {
  border-radius: 50%;
  box-shadow: 0px 3px 12px rgba(0, 0, 0, 0.5);
}
.card-body a {
  text-decoration: none;
}
::selection {
  color: #fff;
  background: #000000;
}
nav {
  font-family: "Roboto Cn", sans-serif;
}
nav ul li .inner {
  color: #000;
  font-weight: 500px;
  font-size: 21px;
  cursor: pointer;
}
nav ul li .inner::hover {
  text-shadow: 0px 2px 100px rgba(0, 0, 0, 0.2);
}
ul li a {
  text-decoration: none;
  color: #000;
}

nav {
  padding-top: 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-transform: uppercase;
  font-size: 1.6rem;
}
nav h1 a {
  text-decoration: none;
}

.brand {
  font-size: 1.8rem;
  font-weight: 500;
  color: #000;
  transform: translateX(-100rem);
  -webkit-animation: slideIn 0.5s forwards;
          animation: slideIn 0.5s forwards;
}

.brand a {
  color: #000;
  text-decoration: none;
  transition: 0.2s;
  cursor: pointer;
}
.brand a:hover {
  text-shadow: 0px 2px 10px rgba(0, 0, 0, 0.2);
  transition: 0.2s;
  cursor: pointer;
}

nav ul {
  display: flex;
}

nav ul li {
  list-style: none;
  transform: translateX(100rem);
  -webkit-animation: slideIn 0.5s forwards;
          animation: slideIn 0.5s forwards;
}

ul li a {
  text-decoration: none;
}
nav ul li a {
  padding: 1rem 0;
  margin: 0 3rem;
  position: relative;
  letter-spacing: 1px;
}

nav ul li a:last-child {
  margin-right: 50px;
  text-decoration: none;
}

nav ul li a::before,
nav ul li a::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 2px;
  background-color: #262626;
  left: 0;
  transform: scaleX(0);
  transition: all 0.5s;
}

nav ul li a::before {
  top: 10px;
  transform-origin: left;
}

nav ul li a::after {
  bottom: 10px;
  transform-origin: right;
}
.navlinks::hover {
  text-shadow: 0px 2px 10px rgba(0, 0, 0, 0.2);
  color: #000;
}
nav ul li a::hover {
  text-shadow: 0px 2px 10px rgba(0, 0, 0, 0.2);
  color: #000;
}

nav ul li a:hover::before,
nav ul li a:hover::after {
  transform: scaleX(1);
}

