  from {
  }
  to {
    transform: translateX(0);
  }
}

@keyframes slideIn {
  from {
  }
  to {
    transform: translateX(0);
  }
}


.data-card .boxer-card:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #4e73df;
  -webkit-clip-path: circle(10px at 125% 10%);
          clip-path: circle(10px at 125% 10%);
  transition: 0.5s ease-in-out;
  z-index: 1;
}

.data-card .boxer-card:hover:before {
  -webkit-clip-path: circle(700px at 80% -20%);
          clip-path: circle(700px at 80% -20%);
  
}
.data-card .boxer-card:hover .index-top{
  color: #fff;
  transition: 0.8s;
}

.data-card .boxer-card:after {
