  position: absolute;
  top: 55%;
  left: 20%;
  font-size: 5.5em;
  font-weight: 800;
  font-style: italic;
  color: rgba(0, 0, 0, 0.2);
  z-index: 2;
}
.index-top {
  z-index: 3000000000000000;
}
.blink {
  z-index: 10000000000000000;
}

.about-matter{
  line-height: 39px;
}

@media screen and (max-width: 700px){

	.menu-toggle{
		display: block;
	}

	nav{
		padding-top: 0;
		display: none;
		flex-direction: column;
		justify-content: space-evenly;
		align-items: center;
		height: 100vh;
		text-align: center;
	}

	nav ul{
		flex-direction: column;
	}

	nav ul li{
		margin-top: 5rem;
	}

	nav ul li a{
		margin: 10px;
		font-size: 2.5rem;
	}

	.brand{
    font-size: 5rem;
  }
  
  .overlay.menu-open,
  nav.menu-open{
	  display: flex;
	  transform: scale(1);
	  opacity: 1;
  }
  
}
.footer{
  position: absolute;
  bottom: -5%;
  left: 50%;
  transform: translate(-50%,-50%);
}
